:root {
  --primary-color: #171c28;
  --secondary-color: #dda448;
  --tertiary-color: #6b818c;
  --text-color: #eff9f0;

  --font-sans: sans-serif;
  --font-mono: monospace;
}

.body {
  overflow-y: scroll;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  scrollbar-color: var(--secondary-color) var(--primary-color);
}

.header {
  width: 90vw;
  background-color: rgba(23, 28, 40, 0.85);
  position: fixed;
  z-index: 1;
  padding-left: 80px;

  margin-right: 80px;
}

.content {
  height: fit-content;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--primary-color);
  color: var(--text-color);
  padding-top: 45rem;
}

.side-display-left {
  height: 100vh;
  width: 40px;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.side-display-left-icon {
  padding-bottom: 1rem;
  font-size: 1.5rem;
  color: var(--tertiary-color);
  transition: all 0.2s ease-in;
}

.side-display-left-icon:hover {
  color: var(--secondary-color);
  transform: translate(0, -5px);
}

.side-display-right {
  height: 100vh;
  width: 40px;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: baseline;
  align-items: center;
  writing-mode: vertical-rl;
  padding-right: 40px;
}

.side-display-right a {
  margin-left: 5px;
  padding: 1rem;
  letter-spacing: 0.1rem;
  writing-mode: vertical-rl;
  text-decoration: none;
  font: 1.25rem var(--font-mono);
  color: var(--tertiary-color);
  transition: all 0.2s ease-in-out;
}

.side-display-right a:hover {
  color: var(--secondary-color);
  transform: translate(0, -5px);
}

.vertical-line-top {
  height: 100%;
  width: 1px;
}

.vertical-line-bottom {
  height: 10%;
  width: 1px;
  background-color: var(--tertiary-color);
}

.vertical-line-bottom-right {
  height: 20%;
  width: 1px;
  background-color: var(--tertiary-color);
}


/* Additional styles for side displays on wider screens (adjust the width as needed) */
@media screen and (min-width: 768px) {
  .side-display-left,
  .side-display-right {
    width: 40px; /* You can adjust this width or add other styles as needed for wider screens */
  }
}

/* Hide side displays on narrow screens */
@media screen and (max-width: 767px) {
  .side-display-left,
  .side-display-right {
    display: none;
  }
}